// Datos de los módulos técnicos
let technicalModules = [
    { name: 'Módulo Técnico 1', professors: [] },
    { name: 'Módulo Técnico 2', professors: [] }
];

// Datos de los profesores técnicos
const technicalProfessors = ['Jesús Técnico', 'Tomás Técnico'];

// Función para cargar los módulos técnicos
function cargarModulosTecnicos() {
    const technicalModulesContainer = document.getElementById('technical-modules');
    technicalModules.forEach(module => {
        const moduleElement = document.createElement('div');
        moduleElement.textContent = module.name;
        moduleElement.classList.add('module-item');
        moduleElement.setAttribute('data-module-name', module.name);
        moduleElement.addEventListener('drop', drop);
        moduleElement.addEventListener('dragover', allowDrop);
        technicalModulesContainer.appendChild(moduleElement);
    });
}

// Función para cargar los profesores técnicos
function cargarProfesoresTecnicos() {
    const technicalProfessorsContainer = document.querySelector('.technician-professors');
    technicalProfessors.forEach(professor => {
        const professorElement = document.createElement('div');
        professorElement.textContent = professor;
        professorElement.classList.add('professor', 'technician');
        professorElement.draggable = true;
        professorElement.addEventListener('dragstart', dragStart);
        professorElement.addEventListener('dragend', dragEnd);
        technicalProfessorsContainer.appendChild(professorElement);
    });
}

// Función para permitir el soltar
function allowDrop(event) {
    event.preventDefault();
}

// Función para soltar el elemento
function drop(event) {
    const moduleName = event.target.textContent;
    const professorName = event.dataTransfer.getData('text');
    
    // Encontrar el módulo correspondiente
    const module = technicalModules.find(module => module.name === moduleName);

    if (module && !module.professors.includes(professorName)) {
        module.professors.push(professorName);
        event.target.textContent += `\n${professorName}`;
    }
}

// Definir la función cuando inicia el arrastre
function dragStart(event) {
    event.dataTransfer.setData('text', event.target.textContent.trim());
}

// Definir la función cuando termina el arrastre
function dragEnd() {
    // No es necesario hacer nada en este caso
}

cargarModulosTecnicos();
cargarProfesoresTecnicos();
